<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link><span>/</span
        ><span>发布话题</span>
      </template>
      <template #content>
        <form action="">
          <div>
            <span>请选择板块:</span>
            <select v-model="post.tab" name="" id="">
              <option value="ask">问答</option>
              <option value="share">分享</option>
              <option value="job">招聘</option>
              <option value="dev">客户端测试</option>
            </select>
          </div>
          <div>
            <input
              class="create-title"
              v-model="post.title"
              type="text"
              placeholder="标题字数10字以上"
            />
          </div>
          <div>
            <mavon-editor
              defaultOpen="edit"
              :subfield="false"
              :toolbars="toolbars"
              v-model="post.content"
            />
          </div>
          <el-button @click="submit" type="primary" size="mini">提交</el-button>
        </form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  components: { Panel },
  data() {
    return {
      post: {
        title: "",
        tab: "",
        content: "",
      },
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
      },
    };
  },
  methods: {
    async submit() {
      //   首先要判断输入的内容是否合理
      let { tab, title, content } = this.post;
      // content = "<div class='markdown-tetx'>"+content+'</div>'

      // 取本地的token
      const accesstoken = localStorage.getItem("token");
      const res = await axios.post("/topics", {
        tab,
        title,
        content,
        accesstoken,
      });
      // console.log(res);
      this.$router.push(`/PostContent/${res.topic_id}`);
    },
  },
};
</script>

<style>
.create-title{
  width: 98%;
  height: 26px;
  outline: none;
  margin-top: 5px;
}
</style>